/*------------------------------------------------------NOT FOUND 404*/
#not-found {
    color: crimson;
    text-align: center;
    margin: 20px auto;
    width: 200px;
}

/*------------------------------------------------------HOMEPAGE*/
#homepage {
    margin: 10px auto;
    width: 40%;
    display: grid;
    grid-template-columns: 0.8fr 1fr;
}

.homepage-text-category ul {
    padding: 0 20px;
}
.homepage-text-category li, .homepage-text-category p {
    margin: 7px 2px;
    font-size: 1.2em;
}
.homepage-text-category h1 {
    margin-top: 25px;
    font-size: 1.8em;
}

#homepage-image {
    display: flex;
    justify-content: center;
}

#homepage-image img {
    width: 75%;
    border-radius: 20px;
    border: 2px black solid;
}

/*------------------------------------------------------SEARCH / SØK*/
#search-results {
    width: 100%;
}

#result {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}

#search {
    width: 50%;
    margin: 20px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}

#search-table, .search-tr th, .search-tr td {
    border-bottom: 1px solid black;
    border-collapse: collapse;
    padding: 7px 10px;
}

.search-tr th {
    text-align: start;
}

#search-table {
    width: 90%;
}

#trinn-th {
    width: 20%;
}

#search-form {
    width: 100%;
    display: flex;
    justify-content: center;
    gap: 10px;
}

#search-form input {
    font-size: 1em;
    width: 75%;
    padding: 5px 7px;
    border-radius: 7px;
    border: 1px solid black;
}
#search-form input:focus {
    border: 2px solid black;
}

/*------------------------------------------------------ALL COURSES OVERVIEW / FAG*/
#courses-overview {
    width: 50%;
    padding: 10px;
    margin: 5px auto;
}

#courses-overview-table, #courses-overview-table th, #courses-overview-table td {
    border-bottom: 1px solid black;
    text-align: start;
    border-collapse: collapse;
}
#courses-overview-table {
    width: 100%;
}
#courses-overview-table th, #courses-overview-table td {
    padding: 10px;
}

/*------------------------------------------------------SPESIFIC COURSE / SINGLE COURSE*/
#single-course-view {
    width: 80%;
    margin: 15px auto;

    display: grid;
    grid-template-columns: 6fr 1fr;
    gap: 20px;
}

#video-player iframe {
    width: 100%;
    aspect-ratio: 16 / 9;
    border: 2px solid black;
    border-radius: 15px;
}

#video-list {
    border: 2px solid black;
    padding: 5px;
    border-radius: 15px;
    max-width: 300px;
    max-height: 80vh;
    width: 100%;
    overflow-y: auto;
}

#video-info {
    margin-top: 5px;
    width: 100%;
    display: flex;
    justify-content: space-between;
}

#video-info h1 {
    width: 75%;
}
#video-info div {
    width: 20%;
    display: flex;
    gap: 10px;
    justify-content: right;
    align-items: start;
}

#video-info div button {
    box-sizing: border-box;
    appearance: none;
    height: min-content;
    width: min-content;
    text-align: center;
    padding: 5px 10px;
    outline: none;
    border: 1px solid black;
    color: black;
    border-radius: 5px;
}

#video-info div button:hover {
    cursor: pointer;
    opacity: .9;
}
#video-info div button:active {
    opacity: .5;
    cursor: unset;
}

.category-overview {
    border-radius: 5px;
    background-color: lightgray;
    border: solid 1px black;
    font-size: 1.1em;
    margin-top: 5px;
}

.spesific-video-sidemenu {
    padding: 7px 3px;
    border-top: 1px black solid;
    border-radius: 0 0 5px 5px;
    font-size: 1.03em;
}

.spesific-video-sidemenu a {
    color: black;
    text-decoration: none;
}

.spesific-video-sidemenu:hover, .category:hover {
    cursor: pointer;
    opacity: 0.75;
}

.spesific-video-sidemenu:active, .category:active {
    cursor: unset;
    opacity: 0.5;
}

.category {
    display: grid;
    grid-template-columns: 3fr 1fr;
    align-items: center;
}

.category div p {
    font-size: 0.7em;
}

.category div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 4px;
    margin: 2px 2px 2px 0;
}

/*------------------------------------------------------OTHER*/
.error {
    color: crimson;
}

/*------------------------------------------------------Media queries*/

@media (max-width: 1227px) {
    /*------------------------------------------------------NOT FOUND 404*/
    /*------------------------------------------------------HOMEPAGE*/
    #homepage {
        margin: 10px auto;
        width: 70%;
        display: grid;
        grid-template-columns: unset;
        grid-template-rows: auto auto;
        gap: 10px;
    }
    /*------------------------------------------------------SEARCH / SØK*/
    /*------------------------------------------------------ALL COURSES OVERVIEW / FAG*/
    /*------------------------------------------------------SPESIFIC COURSE / SINGLE COURSE*/
    #single-course-view {
        width: 85%;
        margin: 15px auto;
        
        display: grid;
        grid-template-columns: unset;
        grid-template-rows: 1fr auto;
        gap: 20px;
    }
    #video-info h1 {
        width: 60%;
    }
    #video-info div {
        width: 35%;
    }

    #video-list {
        width: 50%;
        margin: 0 auto;
        max-height: 400px;
    }
    /*------------------------------------------------------OTHER*/

}

@media (max-width: 767px) {
    /*------------------------------------------------------NOT FOUND 404*/
    /*------------------------------------------------------HOMEPAGE*/
    /*------------------------------------------------------SEARCH / SØK*/
    #search {
        width: 90%;
    }

    /*------------------------------------------------------ALL COURSES OVERVIEW / FAG*/
    #courses-overview {
        width: 90%;
    }

    /*------------------------------------------------------SPESIFIC COURSE / SINGLE COURSE*/
    #video-list {
        width: 100%;
    }

    #single-course-view {
        width: 95%;
    }
    
    /*------------------------------------------------------OTHER*/
}