/* GENERAL*/
* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

main {
  width: 100%;
}


.anti-select {
  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
}

/* PAGE */
.App {
  width: 100%;
  min-height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr auto;
}

/* HEADER */
header {
  background-color: #94b9ff;
  padding: 5px 10px;
  max-width: 100%;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

header img {
  height: 75px;
}

header img:hover {
  cursor: pointer;
}

header img:active {
  cursor: unset;
  opacity: 0.6;
}

.nav-display-wide {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}

.hamburger-parent {
  position: relative;
}

.header-link-option {
  text-decoration: unset;
  color: black;
  padding: 1px 4px;
  border-bottom: 2px darkslategray solid;
  transition: all 0.2s;
}

.header-link-option:hover {
  cursor: pointer;
  padding: 1px 6px;
}

.search-bar {
  font-size: 0.85em;
  padding: 5px;
  border-radius: 5px;
  border: solid 1px black;
  background-color: unset;
}
.search-bar::placeholder {
  color: rgb(80, 80, 80);
}

.search-bar:focus {
  outline: none;
  border: solid 2px black;
}

/* FOOTER */
footer {
  background-color: #7290c7;
  padding: 10px 15px;
  color: white;
  max-width: 100%;
  text-align: center;
}
footer a {
  color: whitesmoke;
}

@media (max-width: 767px) {
  .nav-display-wide {
    flex-direction: column;
    gap: 10px;

    position: absolute;
    bottom: -70px;
    right: 0;
    height: 60px;

    width: 100%;
    padding: 5px;

    border-bottom: 1px solid black;
    border-left: 1px solid black;
    border-right: 1px solid black;
    border-radius: 5px;

    background-color: #94b9ff;
  }
}